import React from "react";
// import styles from './card.module.css';
import styles from "./styles.module.css";

class Card extends React.PureComponent {
  render() {
    const { carditem } = this.props;
    return (
      <>
        <div className={styles.card}>
          <img src={carditem.imgsrc} alt="mypic1" className={styles.cardImg} />
          <div className={styles.cardInfo}>
            <h4 className={styles.cardTitle}>{carditem.title}</h4>
            <div className={styles.autthorDateSection}>
              <span className={styles.cardAuthor}>{carditem.author}</span>
              <span className={styles.cardDate}>|{carditem.date}</span>
            </div>
            <div className={styles.cardDesc}>
              <p>{carditem.desc}</p>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Card;
