import React from "react";
// import '.../styles.css'
// import styles from './Header.module.css'
import styles from "./styles.module.css";
class Header extends React.Component {
  render() {
    return (
      <>
        <header>
          <nav className={styles.nav}>
            <div className={styles.left}>
              <div className={styles.logo_logo} id={styles.logo_light}>
                EDYODA
              </div>
              <br />
              <span className={styles.headerItalics}>Stories</span>

              <div className={styles.headerDark}>
                Explore Categories{" "}
                <i class="fa-solid fa-circle-chevron-down"></i>
              </div>
            </div>
            <div className={styles.right}>
              <div className={styles.headerGray}>
                <p>
                  EdYoda is free learning and knowledge
                  <br />
                  sharing platform for techies
                </p>
              </div>

              <button className={styles.headerBtn}>Go to main website</button>
            </div>
          </nav>
        </header>
      </>
    );
  }
}
export default Header;
